import React, { useState } from 'react';
import axios from 'axios'; // Using axios for uploads but provide a fetch fallback

// Simple styles for this component
const styles = {
  uploadContainer: {
    border: '2px dashed #ccc',
    borderRadius: '8px',
    padding: '20px',
    textAlign: 'center',
    cursor: 'pointer',
    backgroundColor: '#fafafa'
  },
  input: {
    display: 'none'
  },
  imagePreview: {
    maxWidth: '100%',
    maxHeight: '300px',
    marginTop: '20px',
    borderRadius: '4px'
  },
  result: {
    marginTop: '10px',
    fontSize: '12px',
    color: '#333',
    wordBreak: 'break-all'
  }
};


const ImageUpload = ({ userId }) => {
  const [image, setImage] = useState(null);
  const [preview, setPreview] = useState('');
  const [loading, setLoading] = useState(false);
  const [resultUrl, setResultUrl] = useState('');
  const [error, setError] = useState('');

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setImage(file);
      setPreview(URL.createObjectURL(file));
      setResultUrl(''); 
      setError(''); 
    }
  };

  const handleUpload = async () => {
    if (!image) return;

    setLoading(true);
    setError('');

    const formData = new FormData();
    formData.append('file', image);

    // Read Cloudinary config from env with sensible defaults to avoid hardcoding
    const CLOUD_NAME = process.env.REACT_APP_CLOUDINARY_CLOUD_NAME || 'dj4jtjz';
    const UPLOAD_PRESET = process.env.REACT_APP_CLOUDINARY_UPLOAD_PRESET || 'agri_test';
    formData.append('upload_preset', UPLOAD_PRESET);

    const cloudinaryUrl = `https://api.cloudinary.com/v1_1/${CLOUD_NAME}/image/upload`;

    try {
      // Try axios first
      const response = await axios.post(cloudinaryUrl, formData, {
        headers: {
          // Helps some CORS setups and makes request look like XHR
          'X-Requested-With': 'XMLHttpRequest'
        },
        timeout: 120000
      });

      if (response?.data?.secure_url) {
        console.log('Image uploaded to Cloudinary (axios):', response.data.secure_url);
        setResultUrl(response.data.secure_url);
        setError('');
        return;
      }

      // If axios did not return a secure_url, fall through to fetch fallback
      console.warn('Axios upload did not return secure_url, falling back to fetch');

    } catch (err) {
      // Provide richer error messages for investigation
      const status = err.response?.status;
      const serverMessage = err.response?.data || err.response?.data?.error || undefined;
      console.error('Axios upload error', { status, serverMessage, message: err.message });
      // continue to try fetch fallback so user can get more info
    }

    // Fetch fallback to get a different error surface (useful for CORS/network issues)
    try {
      const fetchResponse = await fetch(cloudinaryUrl, {
        method: 'POST',
        body: formData,
        headers: {
          'X-Requested-With': 'XMLHttpRequest'
        }
      });

      const data = await fetchResponse.json().catch(() => null);

      if (fetchResponse.ok && data?.secure_url) {
        console.log('Image uploaded to Cloudinary (fetch):', data.secure_url);
        setResultUrl(data.secure_url);
        setError('');
      } else {
        const msg = data?.error?.message || data?.message || `HTTP ${fetchResponse.status}`;
        console.error('Fetch upload failed', { status: fetchResponse.status, body: data });
        setError('Upload failed: ' + msg);
      }

    } catch (err) {
      console.error('Fetch upload error', err);
      setError('Network or CORS error: ' + (err.message || String(err)));
    } finally {
      setLoading(false);
    }
  };

  return (
    <div>
      <input 
        type="file" 
        id="file-upload"
        accept="image/png, image/jpeg"
        style={styles.input} 
        onChange={handleFileChange}
      />
      
      <label htmlFor="file-upload" style={styles.uploadContainer}>
        {preview ? (
          <img src={preview} alt="Preview" style={styles.imagePreview} />
        ) : (
          <p>Click here to select an image</p>
        )}
      </label>

      <button 
        onClick={handleUpload} 
        disabled={!image || loading}
        style={{marginTop: '10px', padding: '10px 15px'}}
      >
        {loading ? 'Analyzing...' : 'Analyze Crop Health'}
      </button>

      {error && (
        <div style={{...styles.result, color: 'red', marginTop: '10px'}}>
          <p><strong>Upload Failed:</strong> {error}</p>
        </div>
      )}

      {resultUrl && (
        <div style={styles.result}>
          <p><strong>Upload Successful!</strong></p>
          <p>Image URL: {resultUrl}</p>
          <p>(Next, this URL will be sent to the AI model)</p>
        </div>
      )}
    </div>
  );
};

export default ImageUpload;