import React, { useState, useEffect } from 'react';
import './App.css';
// 1. Correct import path (it's in the same folder as App.js)
import { auth } from './components/firebaseConfig'; // Import auth
import { onAuthStateChanged, signOut } from 'firebase/auth'; 
import Auth from './components/Auth';
// 2. Import the new Dashboard component
import Dashboard from './components/Dashboard'; 

function App() {
  const [user, setUser] = useState(null); 
  const [loading, setLoading] = useState(true); 

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser); 
      setLoading(false); 
    });

    return () => unsubscribe();
  }, []);

  const handleLogout = async () => {
    try {
      await signOut(auth);
    } catch (err) {
      console.error("Error signing out: ", err);
    }
  };

  if (loading) {
    return (
      <div className="app-container">
        <h1>Loading Agri-Bot...</h1>
      </div>
    );
  }

  return (
    <div className="app-container">
      {user ? (
        // 3. This part is now updated to show the Dashboard
        <div>
          <header className="app-header">
            <h1>Welcome, {user.displayName || user.email}!</h1>
            <button onClick={handleLogout} className="logout-button">Logout</button>
          </header>
          {/* 4. Render the Dashboard component and pass the user info */}
          <Dashboard user={user} /> 
        </div>
      ) : (
        // If not logged in, show Auth
        <Auth />
      )}
    </div>
  );
}

export default App;