import React,
 { useState } from 'react';
import { auth } from './firebaseConfig'; // Import auth
import { 
  createUserWithEmailAndPassword, 
  signInWithEmailAndPassword,
  GoogleAuthProvider,
  signInWithPopup
} from "firebase/auth";

// A simple CSS-in-JS style object for our component
const styles = {
  authContainer: {
    maxWidth: '400px',
    margin: '50px auto',
    padding: '20px',
    backgroundColor: '#ffffff',
    borderRadius: '8px',
    boxShadow: '0 4px 12px rgba(0,0,0,0.05)',
    textAlign: 'center'
  },
  form: {
    display: 'flex',
    flexDirection: 'column',
    gap: '10px'
  },
  input: {
    padding: '12px',
    fontSize: '16px',
    border: '1px solid #ccc',
    borderRadius: '4px'
  },
  button: {
    padding: '12px',
    fontSize: '16px',
    border: 'none',
    borderRadius: '4px',
    cursor: 'pointer',
    backgroundColor: '#2c5a2c', // Dark Green
    color: 'white'
  },
  googleButton: {
    backgroundColor: '#4285F4', // Google Blue
    marginTop: '10px'
  },
  toggleButton: {
    background: 'none',
    border: 'none',
    color: '#2c5a2c',
    textDecoration: 'underline',
    cursor: 'pointer',
    marginTop: '10px'
  },
  error: {
    color: 'red',
    marginTop: '10px'
  }
};

const Auth = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [isRegistering, setIsRegistering] = useState(false);
  const [error, setError] = useState('');

  const handleAuth = async (e) => {
    e.preventDefault();
    setError(''); // Clear previous errors
    try {
      if (isRegistering) {
        // Create a new user
        await createUserWithEmailAndPassword(auth, email, password);
      } else {
        // Sign in an existing user
        await signInWithEmailAndPassword(auth, email, password);
      }
      // On success, the main app will handle the redirect (we'll set this up next)
    } catch (err) {
      console.error(err);
      setError('Failed to ' + (isRegistering ? 'register' : 'log in') + '. Please check your credentials.');
    }
  };

  const handleGoogleSignIn = async () => {
    setError('');
    const provider = new GoogleAuthProvider();
    try {
      await signInWithPopup(auth, provider);
    } catch (err) {
      console.error(err);
      setError('Failed to sign in with Google.');
    }
  };

  return (
    <div style={styles.authContainer}>
      <h2>{isRegistering ? 'Create Account' : 'Login'}</h2>
      {error && <p style={styles.error}>{error}</p>}
      <form onSubmit={handleAuth} style={styles.form}>
        <input 
          type="email" 
          placeholder="Email" 
          value={email} 
          onChange={(e) => setEmail(e.target.value)} 
          required 
          style={styles.input}
        />
        <input 
          type="password" 
          placeholder="Password" 
          value={password} 
          onChange={(e) => setPassword(e.target.value)} 
          required 
          minLength="6"
          style={styles.input}
        />
        <button type="submit" style={styles.button}>
          {isRegistering ? 'Register' : 'Login'}
        </button>
      </form>
      
      <button 
        style={{...styles.button, ...styles.googleButton}} 
        onClick={handleGoogleSignIn}
      >
        Sign in with Google
      </button>

      <button 
        style={styles.toggleButton} 
        onClick={() => setIsRegistering(!isRegistering)}
      >
        {isRegistering ? 'Already have an account? Login' : "Don't have an account? Register"}
      </button>
    </div>
  );
};

export default Auth;